    fn_search: function() {
      if (this.searchText) {
        console.log("search", this.placeholderText, ": ", this.searchText);
      } else {
        console.log("type write value");
      }
    },
    fn_menu: function() {
      if (!this.isMenu) {
        this.isMenu = true;
      } else {
        this.isMenu = false;
      }
    },
    fn_logout: function() {
      console.log("logout");
    },
    fn_error: function(event) {
      console.log(event);
    },
    Upper: function(value) {
      return value.toUpperCase();
    },
    Lower: function(value) {
      return value.toLowerCase();
    }
  },
  created() {
    //console.log('App created', this.$route.name)
    window.addEventListener("resize", this.handleResize);
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
  computed: {
    cartItemsLength() {
      return this.$store.state.cartProducts.length;
    }
  },
  watch: {
    $route(to, from) {
      console.log(from.name, " --> ", to.name);
